<template>
  <div class="homecoming">
    <div class="black-shadow">
      <div class="homecoming-center">
        <vue-typed-js 
          :strings="['Hi!', `I'm singgih putra pratama`, `I'm programmer`, 'Let me show you about me :)']"
          :typeSpeed="100"
          :startDelay="2000"
          :backDelay="1500"
          v-if="showTyping"
          @onComplete="complete()"
        >
          <h1 class="typing"></h1>
        </vue-typed-js>
      </div>
      <transition name="button-fade">
        <div class="homecoming-button" v-show="showButton">
          <b-button type="is-dark" tag="router-link" to="/profile">Go</b-button>
        </div>
      </transition>
    </div>
    <b-loading :active.sync="isLoading" :can-cancel="false"></b-loading>
  </div>
</template>

<script>
export default {
  data: () => ({
    showTyping: false,
    isLoading: true,
    showButton: false
  }),
  methods: {
    onLoad() {
      this.showTyping = true
      this.isLoading = false
    },
    complete() {
      this.showButton = true
    }
  },
  created: function() {
    const self = this
    const readyStateCheckInterval = setInterval(function() {
      if (document.readyState === "complete") {
        clearInterval(readyStateCheckInterval)
        self.onLoad()
      }
    }, 100);
  }
}
</script>