import Vue from 'vue'
import App from './App.vue'
import Buefy from 'buefy'
import 'buefy/dist/buefy.css'
import './assets/custom.css'
import VueTypedJs from 'vue-typed-js'
import router from './router'

Vue.use(VueTypedJs)
Vue.use(Buefy)
Vue.config.productionTip = false

new Vue({
  router: router,
  render: h => h(App),
}).$mount('#app')
