import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeComing from '@/pages/Homecoming'
import HomePage from '@/pages/Homepage'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: HomeComing
    },
    {
      path: '/profile',
      component: HomePage
    }
  ]
})

export default router